.accordion {
  padding-top: 40px;
  padding-bottom: 35px;
  
  &__el {
    display: flex;
    flex-direction: column-reverse;
    align-items: center;
  }

  &__head {
    cursor: pointer;
  }

  &__body {
    height: 0;
    overflow: hidden;
    margin: 0;
    transition: 0.3s;
  }
}
